@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap);
/* base styles */
html, body, #root {
  height: 100%;
}
body {
  font-family: Raleway, sans-serif;
  margin: 0;
  font-size: 1.4em;
  background: #dfdfdf;
  color: #333;
}
h1,h2,h3,p {
  margin: 0;
}
input, textarea {
  color: #333;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #d4d4d4;
  background-color: #fff;
  display: block;
  width: 100%;
  box-sizing: border-box;
}
.page-title {
  text-align: center;
  margin: 40px auto;
  color: #333;
}
.loading, .error {
  text-align: center;
  margin: 40px auto;
}
.App {
  min-height: 100%;
}

.App.dark {
  background: #333;
}

.App.dark {
  color: rgb(232, 219, 219);
}


.navbar {
    background: #58249c;
    padding: 17px;
    color: #fff;
}


.navbar nav {
    display: flex;
    align-items: center;
    max-width: 1200px;
    margin: 0 auto;
}

.navbar a.brand {
    margin: auto;
    color: #fff;
    text-decoration: none;
}

.navbar a.brand:hover {
    color: rgb(25, 23, 23);
}

.navbar a:last-child {
    color: #fff;
    text-decoration: none;
    margin-left: 40px;
    padding: 8px;
    border: 1px solid #fff;
    border-radius: 4px;
}

.navbar a:last-child:hover {
    background: #fff;
    color: #333;
}
.searchbar form {
    display: flex;
    align-items: center;
}

.searchbar input {
    margin-left: 10px;
}
.hero-info {
    flex-direction: column;
    flex-wrap: wrap;
    width: 35vw;
    align-items: center;
    margin-left: 165px;
    border: 2px solid black;
    text-align: center;
    padding-bottom: 25px;
}


.hero-roles {
    list-style-type: none;
}

.hero-roles-ul {
    padding: 0;
    margin: 0;
}

.hero-img {
    margin-top: 25px;
    border: 2px solid black;
}

.title-hero-page{
    font-weight: bold;
    padding-top: 10px;
}




.hero-list { 
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 60vw;
}

.images-heroes {
    width: 200px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-right: 8px;
    padding-left: 8px;
    transition: -webkit-transform 0.075s ease-in-out;
    transition: transform 0.075s ease-in-out;
    transition: transform 0.075s ease-in-out, -webkit-transform 0.075s ease-in-out;
}

.images-heroes:hover {
    -webkit-transform: scale(1.33);
            transform: scale(1.33);
    cursor: pointer;
}
.home {
    max-width: 1200px;
    margin: 60px auto;
}
.create {
    color: #555;
    max-width: 480px;
    margin: 60px auto;
}

.create label span {
    display: block;
    margin: 30px 0 10px;
}

.create p {
    margin-top: 10px;
    font-size: 0.8em;
}

.create button {
    display: block;
    width: 100px;
    font-size: 1em;
    color: #fff;
    padding: 8px 8px;
    border: 0;
    border-radius: 4px;
    background-color: #58249c;
    cursor: pointer;
    text-decoration: none;
    margin: 20px auto;
    text-align: center;
}

.create button:hover {
    color: black;
}

.ingredients {
    display: flex;
    align-items: center;
}

.ingredients button {
    margin: 0 0 0 10px;
}

.recipe-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
    max-width: 1200px;
    margin: 40px auto;
}


.recipe-list .card {
    background: #fff;
    color: #333;
    padding: 20px;
    border-radius: 4px;
    box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.05);
    transition: all 0.3s ease;
    position: relative;
}

.recipe-list .card:hover {
    -webkit-animation: shaking-around 0.61s;
            animation: shaking-around 0.61s;
}


@-webkit-keyframes shaking-around {

    33% {
        -webkit-transform: rotate(4deg);
                transform: rotate(4deg);
    }

    66% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }

    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}


@keyframes shaking-around {

    33% {
        -webkit-transform: rotate(4deg);
                transform: rotate(4deg);
    }

    66% {
        -webkit-transform: rotate(-4deg);
                transform: rotate(-4deg);
    }

    100% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

.recipe-list .card h3 {
    color: #555;
    margin-bottom: 6px;
}

.recipe-list .card p {
    color: #999;
    font-size: 0.9em;
}

.recipe-list .card div {
    color: #555;
    font-size: 0.7em;
    margin: 20px 0;
    line-height: 1.5em;
}

.recipe-list .card a {
    color: #555;
    text-decoration: none;
    display: block;
    background: #e2e2e2;
    font-size: 0.9em;
    width: 100px;
    text-align: center;
    width: 120px;
    padding: 8px;
    border-radius: 4px;
    margin: 20px auto 0;
}

.recipe-list .card.dark {
    background: #555;
}

.recipe-list .card.dark p,
.recipe-list .card.dark h3,
.recipe-list .card.dark div {
    color: #e4e4e4;
}

.delete {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
    -webkit-filter: invert(60%);
            filter: invert(60%);
}



.guide {
    max-width: 1200px;
    margin: 40px auto;
    text-align: center;
    background: #fff;
    padding: 10px;
    box-sizing: border-box;
}

.guide ul {
    padding: 0;
    justify-content: center;
    margin-top: 25px;
}

.guide li {
    list-style-type: none;
    margin-right: 10px;
    color: #777;
    margin-top: 10px;
}

.guide li::after {
    content: ",";
}

.guide li:last-child::after {
    content: ".";
}

.style {
    text-align: center;
    line-height: 1.5em;
}

.guide.dark { 
    background: #555;
    color: #e4e4e4;
}


.theme-selector {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    max-width: 1200px;
    margin: 20px auto;
}

.theme-buttons div {
    display: inline-block;
    width: 20px;
    height: 20px;
    cursor: pointer;
    margin-left: 15px;
    border-radius: 50%;
}

.mode-toggle {
    margin-right: auto;
}

.mode-toggle img {
    width: 24px;
    height: 24px;
    cursor: pointer;
}

